* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root{
  --primary:  #227439;
  --secondary: rgb(6, 6, 116);

  --white: #fff;
  --normal:400;
  --bold: 700;
  --light: 500;
  --font-family:"DM sans";
}

body {
  overflow-x: hidden;
  max-width: 1450px;
  margin: auto;
  color: #8D8A8A;
}

html {
  font-family: "DM sans", sans-serif;

 
}
ul{
  list-style-type: none;
}
h1{
 font-size: 3.5rem
}

h2{
  font-size: 2rem
}

a{
  text-decoration: none;
}



/* .slick-slider{
  height: 100vh;
}
.slick-dots
{
    position: absolute;
    top: 90%;
    z-index: 5;
} */
.slick-dots li button:before
{
  font-size: .7rem !important;
    opacity: .25;
    color:var(--primary) !important;

}
.slick-dots li.slick-active button:before
{
    opacity: .75;
    color:var(--secondary) !important;
}
.slick-prev,
.slick-next
{
    display: none !important;

}
.slick-next {
  right: 0 !important;
}



@media (max-width: 940px){
  h1{
    font-size:2.5rem;
  }
  h2{
    font-size:1.5rem;
  }
}